.content{max-width:500px;margin:auto;max-height:500px}form.formAlgorithm{display:flex;flex-direction:column;width:500px;min-width:200px;min-height:200px;padding:20px 40px 40px;box-shadow:0 8px 30px #000;background-color:#d3d3d3}form.formAlgorithm>h1{display:flex;justify-content:center;font-size:2.1em;margin-top:.25em;margin-bottom:.9em;color:#000;letter-spacing:1.2px}label{color:#000;position:relative}label,label .tooltiptext{padding:6px;display:inline-block}label .tooltiptext{visibility:hidden;width:auto;background-color:#000;color:#fff;text-align:justify;border-radius:5px;position:absolute;margin-left:8px;opacity:0;transition:opacity .5s}label:hover .tooltiptext{visibility:visible;opacity:1}input.inputAlgorithm{display:flex;flex-direction:column}input.inputAlgorithm,textarea{width:100%;padding:7px;margin-top:5px}.invalid{border:1px solid #db2222;box-shadow:none}input[type=checkbox].invalid{box-shadow:0 0 0 1px #db222b}select.selectAlgorithm{padding:3px;width:100%;height:30px;margin-top:5px}input[type=checkbox]{margin-right:.4em;margin-top:5px;margin-bottom:.2em;width:4%}button{cursor:pointer;padding:10px;border:none;border-radius:4px;background-color:#646c74;color:#fefefe}button:hover{transition:all .2s ease-in-out;background:#000;color:#fff}.btn-group{margin-top:5px;cursor:pointer;float:left}.required{color:#db2269;font-size:.8em;margin-top:.5em}.k-floating-label-container,.k-textbox-container{width:100%}